.par{
    background-color: white;
    height: 66vh;
    width: 78vw;
    display: grid;
    grid-template-columns:repeat(3,1fr);
    gap:5vh;
    padding: 5%;
    overflow-y:scroll;
}
.bk{
    height: 25vh;
    width: 15vw;
}
.booktxt{
    font-family: var(--main-font);
    font-weight: bolder;
}
.booksub{
    font-family: var(--main-font);
    font-weight: lighter;
    font-size: 3vh;
}